.projects-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: row;
}
.description {
  display: none;
  color: black;
  background-color: white;
  margin: 10px;
  padding: 10px;
  border-radius: 10px;
  opacity: 0.85;
}
.project-pic {
  display: flex;
  width: 80vw;
  height: 20vh;
  background-size: cover;
  background-position: center;
  text-align: center;
  &:hover {
    border-radius: 10px;
  }
}
.project-item {
  margin: 10px;
  &:hover {
    transform: scale(1.1);
    transition: all 0.2s ease-in-out;
    .description {
      display: block;
    }
  }
}
@media (min-width: 570px) {
  .project-pic {
    width: 30vw;
    height: 30vh;
  }
}
