@import '../utilities/variables';
* {
  box-sizing: border-box;
}
h1,
p {
  color: $main-black;
}
.text-pink {
  color: salmon;
}
.section {
  justify-content: center;
  padding: 1.5em;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  align-items: center;
}
.title {
  font-size: $title;
  text-align: center;
  margin: 0 0 0.3em 0;
}
.button-outline {
  background-color: white;
  padding: 0.5em 1em;
  font-size: 1.3em;
  font-weight: bold;
  border: 2px solid $main-black;
}
button:focus {
  outline: none;
}
a {
  text-decoration: none;
}
