@import '../../utilities/variables.scss';

#about-me {
  padding: 0;
  align-items: center;
  background: linear-gradient(180deg, salmon 0%, #acb6e5 100%);
}

.about-me-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background-color: white;
  width: 92%;
  margin: 1em 2em;
  height: 100%;
  padding: 1.5em;
}
.profilePicture {
  border-radius: 100%;
}
.cv-button {
  &:hover {
    transform: scale(1.1);
    transition: all 0.2s ease-in-out;
  }
}
.about-me-paragraph {
  text-align: center;
  font-size: 1.3em;
  width: 60%;
}
